<template>
  <div class="lec-details-modal">
    <div class= "overlay-lec-detail valign-text-middle notosanskr-bold-white-24px" @click="$emit('modal-close')">
      <div class = empty-cart notosanskr-bold-white-16px></div>
         
    </div>
    <div class= "lec-details-modal-card">
      <div class= "row">
        <RMenuModifiableTitleBox color="darkYellow" size="440" :text=this.modalData.lec_info.></RMenuModifiableTitleBox>
        <RMenuModifiableTitleBox color="darkYellow" size="100" :text=this.modalData.lec_info. ></RMenuModifiableTitleBox>
      </div>
      <div class= "row">
        <RMenuTitleBox color="yellow" size="240"> </RMenuTitleBox>
        <RMenuTitleBox color="yellow" size="240"></RMenuTitleBox>
        <RMenuTitleBox color="yellow" size="60"></RMenuTitleBox>
      </div>
      <div class= "row">
        <RMenuTextBox color="yellow" size="240" :text=this.modalData.lec_info.></RMenuTextBox>
        <RMenuTextBox color="yellow" size="240" :text=this.modalData.lec_info.></RMenuTextBox>
        <RMenuTextBox color="yellow" size="60" :text='this.modalData.lec_info.'></RMenuTextBox>
      </div>
      <div class= "row">
        <RMenuTitleBox color="yellow" size="90"></RMenuTitleBox>
        <RMenuTitleBox color="yellow" size="90"></RMenuTitleBox>
        <RMenuTitleBox color="yellow" size="180"></RMenuTitleBox>
        <RMenuTitleBox color="yellow" size="90"></RMenuTitleBox>
        <RMenuTitleBox color="yellow" size="90"></RMenuTitleBox>
      </div>
      <div class= "row">
        <RMenuTextBox color="yellow" size="90" :text=this.modalData.lec_info.></RMenuTextBox>
        <RMenuTextBox color="yellow" size="90" :text=this.modalData.lec_info.></RMenuTextBox>
        <RMenuTextBox color="yellow" size="180" :text=this.modalData.lec_info.></RMenuTextBox>
        <RMenuTextBox color="yellow" size="90" :text=this.modalData.lec_info.></RMenuTextBox>
        <RMenuTextBox color="yellow" size="90" :text=this.modalData.lec_info.></RMenuTextBox>
      </div>
      <canvas ref="pieChart">
      </canvas>
      <div v-if="this.modalData.prev_infos.length==0">
           .
      </div>
      <div v-for="(prev, index) in this.modalData.prev_infos" style="border: solid 2px #eea900" :key="prev">
        <div class = "row">
          <RMenuTitleBox color="darkYellow" size="540">{{ prev. }} {{ prev. / 10 }}   {{ prev. }}
            <img class="hide-button" src='hidebutton.svg' @click="isRegisteredShow[index] = !isRegisteredShow[index]" v-show="!this.isRegisteredShow[index]">
            <img class="show-button" src='showbutton.svg' @click="isRegisteredShow[index] = !isRegisteredShow[index]" v-show="this.isRegisteredShow[index]">
          </RMenuTitleBox>
        </div>
        <div v-show="isRegisteredShow[index]">
          <div class= "row">
            <RMenuTitleBox color="yellow" size="95"></RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95"></RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95"></RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95"></RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95"></RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95"></RMenuTitleBox>
          </div>
          <div class= "row">
            <RMenuTextBox color="yellow" size="95" :text=prev.></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.></RMenuTextBox>
          </div>
        </div>
        <div class="row"> 
          <RMenuTitleBox color="darkYellow" size="540">  (: {{ prev. }})
            <img class="hide-button" src='hidebutton.svg' @click="isWantedShow[index] = !isWantedShow[index]" v-show="!this.isWantedShow[index]">
            <img class="show-button" src='showbutton.svg' @click="isWantedShow[index] = !isWantedShow[index]" v-show="this.isWantedShow[index]">
          </RMenuTitleBox>
        </div>
        <div class="detail-info-row" v-show="isWantedShow[index]">
          <div v-for="detailInfo in prev." :key="detailInfo">
            <RMenuTitleBox color="yellow" size="106" fontsize = "16" style="overflow: hidden" @click="isWantedShow[index] = !isWantedShow[index]">{{detailInfo.}}</RMenuTitleBox>
            <RMenuTextBox color="yellow" size="106" fontsize = "16" @click="isWantedShow[index] = !isWantedShow[index]" :text=detailInfo.></RMenuTextBox>
          </div>
        </div>
        <div class= "row">
          <RMenuTitleBox color="darkYellow" size="540">  ~  </RMenuTitleBox>
          <img class="hide-button" src='hidebutton.svg' @click="isRankingShow[index] = !isRankingShow[index]" v-show="!this.isRankingShow[index]">
          <img class="show-button" src='showbutton.svg' @click="isRankingShow[index] = !isRankingShow[index]" v-show="this.isRankingShow[index]">
        </div>
        <div v-show="isRankingShow[index]">
          <div class="row">
            <RMenuTitleBox color="yellow" size="95">1</RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95">2</RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95">3</RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95">4</RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95">5</RMenuTitleBox>
            <RMenuTitleBox color="yellow" size="95">6~</RMenuTitleBox>
          </div>
          <div class="row">
            <RMenuTextBox color="yellow" size="95" :text=prev.1></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.2></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.3></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.4></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.5></RMenuTextBox>
            <RMenuTextBox color="yellow" size="95" :text=prev.5></RMenuTextBox>
          </div>
        </div>
        <div class = "padding"></div>
      </div>
    </div>
  </div>
</template>
  

<script>
import RMenuTitleBox from "./RMenu/Box/RMenuTitleBox";
import RMenuModifiableTitleBox from "./RMenu/Box/RMenuModifiableTitleBox";
import RMenuTextBox from "./RMenu/Box/RMenuTextBox";
import {Chart, registerables} from 'chart.js'
import ChartDataLabels from 'chartjs-plugin-datalabels';
Chart.register(...registerables)

export default {
  name: "Modal",
  props: ["modalData"],
  components: {
    RMenuTitleBox, RMenuTextBox, RMenuModifiableTitleBox,
  },
  computed:{

  },
  data() {
    return {
      isRankingShow : [false, false, false, false, false, false, false, false,],
      isWantedShow : [false, false, false, false, false, false, false, false,],
      isRegisteredShow: [false, false, false, false, false, false, false, false,],
    }
  },
  mounted(){
    this.setChart()
  },
  methods:{
    setData(){
      let data =     
      {
      labels: [],
      datasets: [{
        data: [],
        backgroundColor: [],
        borderColor: [
          'rgba(255, 255, 255, 1)',
        ],
        label: '',
        borderWidth: 2
      }]
    }
      //PF
      if (this.modalData.lec_info['A+'] + this.modalData.lec_info['A0'] + this.modalData.lec_info['B+'] + this.modalData.lec_info['B0'] + this.modalData.lec_info['C+'] + this.modalData.lec_info['C0'] + this.modalData.lec_info['D+'] + this.modalData.lec_info['D0'] == 0)
      {
        data.labels = ['P', 'F']
        data.datasets[0].data.push(this.modalData.lec_info.Pass)
        data.datasets[0].data.push(this.modalData.lec_info.F)
        data.datasets[0].backgroundColor =
        [ '#4BC204',
          '#FF1746',]
      }
      //non-PF
      else
      {
        data.labels = [ 'A+', 'A', 'B+', 'B', 'Others', 'F' ]
        data.datasets[0].data.push(this.modalData.lec_info['A+'])
        data.datasets[0].data.push(this.modalData.lec_info['A0'])
        data.datasets[0].data.push(this.modalData.lec_info['B+'])
        data.datasets[0].data.push(this.modalData.lec_info['B0'])
        data.datasets[0].data.push(this.modalData.lec_info['C+']+this.modalData.lec_info['C0']+this.modalData.lec_info['D+']+this.modalData.lec_info['D0'])
        data.datasets[0].data.push(this.modalData.lec_info['F'])
        data.datasets[0].backgroundColor =
        [ 
          '#097f09',
          '#4BC204',
          '#FEDA00',
          '#FEA000',
          '#FF1746',
          '#747474']

      }
      return data
    },
    setOptions(){
      let options={
        plugins: {
          title: {
            display: true,
            text: this.modalData.lec_info. + '  ',
            position: 'top',
            color: '#000000',
            font: {
              size: 25,
            }
          },
          legend: {
            display: false,
          },
          tooltips: {
            enabled: false
          },
          datalabels: {
            color: '#FFF',
            textAlign: 'center',
            align: top,
            font: {
                size: 25,
            },
            formatter: function(value, ctx) {
                return ctx.chart.data.labels[ctx.dataIndex] + '\n' + value;
            }
          }
        },
        maintainAspectRatio : true,
        animation: false,
      }
      return options
    },
    setChart(){
      new Chart(this.$refs.pieChart, {
        plugins:[ChartDataLabels],
        type:'pie',
        data:this.setData(),
        options:this.setOptions(),
      })
    }
  }
};


</script>

<style lang="sass">
@import '../../variables'

.lec-details-modal
  width: 600px
  height: 960px
  position: absolute

.overlay-lec-detail
  width: 100%
  height: 100%
  position: absolute
  opacity: 0.3
  background-color: $black
  align-items: center  
  border-radius: 15px

    
.lec-details-modal-card
  background-color: $error-color
  position: absolute
  left: 5%
  width: 90%
  height : 80%
  margin: auto
  margin-top: 5%
  background-color: white
  height : 80%
  z-index: 10
  border: 3px solid #eea900
  overflow-y: overlay
  border-top-left-radius: 15px
  border-top-right-radius: 15px

.empty-cart
  width: 100%
  height: 80%
  opacity: 0
  background-color: black

.detail-info-row
  display: inline-flex
  flex-wrap: wrap

.lec-details-modal-card::-webkit-scrollbar 
  width : 0px
  
.lec-details-modal-card::-webkit-scrollbar-thumb
  background-color: rgba(0,0,0)
  height: 95%

.show-button
  cursor: pointer
  height: 30px
  width: 30px
  transition: all 0.2s ease
  position: absolute
  left: 90%
.show-button:hover
  transform: scale(1.1)
.show-button:active
  transform: rotate(90deg) scale(0.7)
.hide-button
  cursor: pointer
  height: 30px
  width: 30px
  transition: all 0.2s ease
  position: absolute
  left: 90%
.hide-button:hover
  transform: scale(1.1)
.hide-button:active
  transform: rotate(-90deg) scale(0.7)

.padding
  height: 30px
  width: 100%

</style>