<template>
  <div class="custom">
    <div class = "custom-upper-menu">
      <RMenuTitleBox color="blue" fontsize = "22">Top 5</RMenuTitleBox>
      <RMenuTextBox color="blue" text = "      Top 5 ."></RMenuTextBox>
    </div>
    <div class = "custom-lower-menu">
      <div class = "major-title">
        <RMenuTitleBox size = "300" color = "blue" style= "border: none">          
          <select class= "select-major-custom" v-model="this.getUserInfo.major" @change="changeMajorType">
            <option v-for="item in this.getMajorData" :value="item" :key="item">{{ item }}</option>
          </select>
        </RMenuTitleBox>
        <RMenuTitleBox size = "100" color = "blue" style= "border: none">          
          <select class= "select-grade-custom" v-model="this.getUserInfo.grade" @change="[changeMajorType(), changeGyoyangType()]">
            <option v-for="item in this.gradeList" :value="item" :key="item">{{ item + ""}}</option>
          </select>
        </RMenuTitleBox>
        <RMenuTitleBox size = "200" color ="blue" fontsize = "22"  style= "border: none">  Top 5</RMenuTitleBox>
      </div>
      <CustomCart :customList = "this.getCustomMajor" v-if="this.getCustomMajor.hot != undefined"/>
      <div class = "gyoyang-title">
        <RMenuTitleBox size = "400" color = "blue" style= "border: none">          
          <select class= "select-ge-custom" v-model="gyoyangType" @change="changeGyoyangType">
            <option v-for="item in this.fieldOrder" :value="item" :key="item">{{ item }}</option>
          </select>
        </RMenuTitleBox>
        <RMenuTitleBox size = "200" color ="blue" fontsize = "22"  style= "border: none">   Top 5</RMenuTitleBox>
      </div>
      <CustomCart :customList = "this.getCustomGE" v-if="this.getCustomGE.hot != undefined"/>
    </div>
  </div>
</template>

<script>
import RMenuTitleBox from "../Box/RMenuTitleBox";
import RMenuModifiableTitleBox from "../Box/RMenuModifiableTitleBox";
import RMenuTextBox from "../Box/RMenuTextBox";
import CustomCart from "./CustomCart"
export default {
  name: "Custom",
  components: {
    RMenuTitleBox, RMenuTextBox, CustomCart, RMenuModifiableTitleBox
  },
  data(){
    return{
      majorType : "",
      gyoyangType : "",
      gradeList: [1, 2, 3, 4],
      fieldOrder : [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      ],
    }
  },
  computed:{
    customList(){
      let filtered = []
      let gradList = this.$store.getters.getGradList
      let customs = this.$store.getters.getCustomList

      for (let gradRec of gradList){
        if (gradRec. == 'n'){
          let transformedName = transformGradName(gradRec.)

          for (let cs of customs){
            if (transformedName.includes(cs.)){
              filtered.push(cs)
              break;
            }
          }
        }
      }
      filtered = filtered.sort((a, b) => fieldOrder.indexOf(a.) - fieldOrder.indexOf(b.));
    },
    getMajorData(){
      return this.$store.getters.getMajorData
    },
    getCustomGE(){
      return this.$store.getters.getCustomGE
    },
    getCustomMajor(){
      return this.$store.getters.getCustomMajor
    },
    getUserInfo(){
      return this.$store.getters.getUserInfo
    }
  },
  methods: {
    setIsShow(fieldName) {
      this.$store.commit("setIsCustomShow", fieldName)
    },
    changeMajorType(){
      this.$store.dispatch('fetchCustomMajor')
    },
    changeGyoyangType(){
      this.$store.dispatch('fetchCustomGE', this.gyoyangType)
    }
    
  }
};
</script>

<style lang="sass">
.custom
  align-items: center
  background-color: #e8f0ff
  display: flex
  flex-direction: column
  height: 100%
  position: relative
  width: 100%
  border-bottom-right-radius: 15px
  border-bottom-left-radius: 15px
.row
  align-items: center
  display: flex

.custom
  overflow-y: overlay
  overflow-x: hidden

.custom::-webkit-scrollbar 
  display: none

.select-major-custom
  width: 85%
  height: 80%
  position: absolute
  top: 10%
  left: 13%
  background-color: #b2c3e1
  font-family: "Noto Sans KR", Helvetica
  font-size: 22px
  border: none
  color: white
  font-weight: 700

.select-grade-custom
  width: 95%
  height: 80%
  top: 10%
  left: 3%
  background-color: #b2c3e1
  font-family: "Noto Sans KR", Helvetica
  font-size: 22px
  border: none
  color: white
  font-weight: 700

.select-ge-custom
  width: 60%
  height: 80%
  position: absolute
  top: 10%
  left: 39%
  background-color: #b2c3e1
  font-family: "Noto Sans KR", Helvetica
  font-size: 22px
  border: none
  color: white
  font-weight: 700

.select-major-custom:hover,
.select-ge-custom:hover,
.select-grade-custom:hover
  transform: scale(1.1)
  opacity: 0.6

.major-title
  display: flex
  flex-direction: row

.gyoyang-title
  display: flex
  flex-direction: row
</style>
